<style scoped>

</style>
<template>
    <div>
        <h2>缴费列表</h2>
        <Divider></Divider>
        <Table stripe border :columns="columns" :data="listData.data"></Table>
        <Spin size="large" fix v-if="load"></Spin>
        <Row v-if="listData.last_page > 1" style="text-align: center;margin-top: 20px">
            <Page @on-change="loadData" :total="listData.total" :page-size="listData.per_page"
                  :current="listData.current_page" size="small"/>
        </Row>


        <Modal
                v-model="showDialog"
                title="选择付款方式"
                :loading="loading">
            <Button size="large" type="primary" ghost style="margin: 10px;width: 180px" @click="toPay(1)">
                <i class="iconfont icon-zhifubao " style="color:#2d8cf0;margin-right: 10PX"></i>
                支付宝支付
            </Button>
            <Button size="large" type="primary" ghost style="margin: 10px;width: 180px" @click="toPay(2)">
                <i class="iconfont icon-wxopen" style="color:#19be6b;margin-right: 10PX"></i>
                微信支付
            </Button>
            <div slot="footer">
            </div>
        </Modal>
    </div>
</template>

<script>
  import api from './api';

  let pingpp = require('pingpp-js');
  export default {
    data() {
      return {
        listData: [],
        formData: {},
        load: false,
        loading: false,
        showDialog: false,
        columns: [
          {
            title: '单号',
            align: 'center',
            key: 'order_no'
          },
          {
            title: '描述',
            align: 'center',
            key: 'description'
          },
          {
            title: '金额',
            align: 'center',
            key: 'price'
          },
          {
            title: '状态',
            align: 'center',
            render: (h, params) => {
              let str;
              switch (params.row.status) {
                case 0:
                case 3:
                  str = '待支付';
                  break;
                case 1:
                  str = '待开票';
                  break;
                case 2:
                  str = '完成';
                  break;
              }
              return h('span', str);
            }
          },
          {
            title: '创建日期',
            align: 'center',
            key: 'create_time'
          },
          {
            title: '操作',
            align: 'center',
            render: (h, params) => {
              return h('Button', {
                props: {
                  size: 'small'
                },
                style: {
                  marginRight: '5px',
                  display: (params.row.status === 0 || params.row.status === 3) ? '' : 'none'
                },
                on: {
                  click: () => {
                    this.pay(params.row.order_no);
                  }
                }
              }, '付款');
            }
          }
        ],
        payItem: {}
      };
    },
    mounted() {
      this.loadData();
      if (this.$route.query.result != null && this.$route.query.out_trade_no != null) {
        this.$Notice.success({
          title: '支付成功',
          desc: '订单' + this.$route.query.out_trade_no + '支付成功'
        });
        this.$router.push({name:'charge'})
      }
    },
    methods: {

      loadData(page) {
        if (page) {
          this.formData.jump_page = page;
        }
        this.load = true;
        api.index(this.formData, res => {
          this.load = false;
          this.listData = res.data.data;
        }, () => {
          this.load = false;
        });
      },
      pay(order) {
        this.payItem.order_no = order;
        this.showDialog = true;
      },
      toPay(type) {
        this.payItem.charge_type = type;
        api.pay(this.payItem, res => {
          console.log(res);
          pingpp.createPayment(res.data, function (result, err) {
          });
        });
      }
    }
  };
</script>
